import React, { useState } from 'react';
import { BrowserRouter as Router, Route, Routes, Link, useLocation } from 'react-router-dom';
import { Button, TextField, Card, CardContent, Select, MenuItem, Tabs, Tab, Typography, Box } from "@mui/material";
import Summary from './components/Summary';
import Chat from './components/Chat';
import ControlMapping from './components/ControlMapping';
import { CssBaseline } from '@mui/material';

const App = () => {
  // Get the current route to sync with Tabs
  const location = useLocation();
  const getCurrentTab = () => {
    switch (location.pathname) {
      case "/chat":
        return 1;
      case "/control-mapping":
        return 2;
      default:
        return 0;
    }
  };

  const [tabIndex, setTabIndex] = useState(getCurrentTab);

  return (
    <>
      <CssBaseline />
      <Box sx={{ width: '100%', bgcolor: 'background.paper' }}>
        <Tabs 
          value={tabIndex} 
          onChange={(event, newValue) => setTabIndex(newValue)}
          centered
        >
          <Tab label="Summary" component={Link} to="/" />
          <Tab label="Chat" component={Link} to="/chat" />
          <Tab label="Control Mapping" component={Link} to="/control-mapping" />
        </Tabs>
        <Routes>
          <Route path="/" element={<Summary />} />
          <Route path="/chat" element={<Chat />} />
          <Route path="/control-mapping" element={<ControlMapping />} />
        </Routes>
      </Box>
    </>
  );
};

const AppWrapper = () => (
  <Router>
    <App />
  </Router>
);

export default AppWrapper;
