import React, { useState } from 'react';
import { Button, Card, CardContent, Typography, Box, CircularProgress, Input, Alert, Snackbar } from "@mui/material";
import { Link } from 'react-router-dom';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import { TreeView, TreeItem } from '@mui/x-tree-view';
import { CheckCircleOutline, EmailRounded, FileCopy, Summarize } from '@mui/icons-material';

const Summary = () => {
  const [files, setFiles] = useState([]);
  const [summary, setSummary] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showProcessNotif, setShowProcessNotif] = useState(false);

  const handleFileUpload = (e) => {
    setFiles(Array.from(e.target.files));
  };

  const summarizeDocument = () => {
    setLoading(true);
    setTimeout(() => {
      setSummary({
        title: "Document Summary",
        brief: `This document explores the transformative effects of Artificial Intelligence (AI) on modern workplaces. It highlights the ways in which AI is reshaping industries, enhancing productivity, and altering job functions. AI is increasingly being used for automating repetitive tasks such as data entry, customer service, and inventory management, allowing employees to focus on more complex and creative responsibilities.
            As AI tools take over routine tasks, employees must adapt by acquiring new skills, particularly in areas like data analysis, programming, and machine learning, to remain relevant in the evolving job market. The widespread use of AI also raises ethical issues, including the potential for job displacement, algorithmic bias, and privacy concerns. Companies are urged to develop responsible AI policies to mitigate these risks.
            The document concludes by predicting that AI will continue to complement human workers rather than replace them entirely, emphasizing the importance of human creativity, emotional intelligence, and complex decision-making. Overall, the document presents a balanced view of AI's potential to drive innovation while also posing challenges that need careful consideration.`,
        themes: [
          {
            id: "theme-1",
            name: "Theme 1",
            subThemes: [
              { id: "subtheme-1.1", name: "Sub Theme 1.1", subThemes: [{ id: "detail-1.1.1", name: "Detail 1.1.1" }, { id: "detail-1.1.2", name: "Detail 1.1.2" }] },
              { id: "subtheme-1.2", name: "Sub Theme 1.2" }
            ]
          },
          {
            id: "theme-2",
            name: "Theme 2",
            subThemes: [
              { id: "subtheme-2.1", name: "Sub Theme 2.1" },
              { id: "subtheme-2.2", name: "Sub Theme 2.2", subThemes: [{ id: "detail-2.2.1", name: "Detail 2.2.1" }] }
            ]
          }
        ]
      });
      setLoading(false);
    }, 2000); // Mock API delay
  };
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setShowProcessNotif(false);
  };

  const processDocument = () => {
    setLoading(true);
    setTimeout(() => {
        setShowProcessNotif(true);
        setLoading(false);
    }, 2000); // Mock API delay
  };

  const renderTree = (nodes) => (
    <TreeItem key={nodes.id} itemId={nodes.id} label={nodes.name}>
      {Array.isArray(nodes.subThemes) ? nodes.subThemes.map((node) => renderTree(node)) : null}
    </TreeItem>
  );

  return (
    <Box sx={{ p: 4, textAlign: 'center' }}>
    
      {/* Document Summary */}
      {
        showProcessNotif ?
            <Snackbar
                anchorOrigin={{vertical: 'top', horizontal: 'right'}}
                onClose={handleClose}
                open={showProcessNotif}
                autoHideDuration={1500}
                message="Document processed successfully"
            />
            :null
      }
      {/* File Upload */}
      <label htmlFor="file-upload">
        <input id="file-upload" type="file" multiple hidden onChange={handleFileUpload} />
        <Button
          component="span"
          variant="contained"
          color="primary"
          startIcon={<CloudUploadIcon />}
        >
          Upload Files
        </Button>
        <Button variant="outlined" color="primary" startIcon={<FileCopy/>} sx={{ ml: 5 }} onClick={processDocument}>
            Process
        </Button>
        {files.length > 0 && (
            <Button variant="outlined" color="primary" startIcon={<Summarize/>} sx={{ ml: 5 }} onClick={summarizeDocument}>
                Summarize
            </Button>
        )}
      </label>

      {/* Show selected files */}
      {files.length > 0 && (
        <Box sx={{ mt: 2 }}>
          <Typography variant="subtitle1">Selected Files:</Typography>
          {files.map((file, index) => (
            <Typography key={index} variant="body2" color='success'>{file.name}</Typography>
          ))}
          
        </Box>
      )}

      {/* Loading Indicator */}
      {loading && (
        <Box sx={{ mt: 3 }}>
          <CircularProgress />
          <Typography variant="body2" sx={{ mt: 1 }}>Processing...</Typography>
        </Box>
      )}

      {/* Summary Output */}
      {summary && !loading && (
        <Card sx={{ m: "20px auto", p: 2, width: 1200 ,textAlign: 'left' }}>
          <CardContent>
          <Typography variant="h5" gutterBottom>
            {summary.title}
            <EmailRounded sx={{ml:1, fontSize:'18px'}}/>
          </Typography>
          <Typography variant="body2" gutterBottom>{summary.brief}</Typography>
            <TreeView>
                {summary.themes.map((theme) => renderTree(theme))}
            </TreeView>
          </CardContent>
        </Card>
       )}

      {/* Action Buttons - Only visible after processing */}
      {summary && !loading && (
        <>
            <Box sx={{ mt: 2 }}>
            <Button variant="outlined">Download</Button>
            <Button component={Link} to="/chat" variant="contained" color="secondary" sx={{ ml: 2 }}>Chat</Button>
            </Box>
            {/* <Box sx={{ mt: 5 }}>
                <Input type='text' placeholder='Enter email ID'></Input>
                <Button variant='contained' sx={{ ml: 4 }}>Send</Button>
            </Box> */}
        </>
      )}
    </Box>
  );
};

export default Summary;
